.users_container {
  width: 80%;
  min-width: 300px;
  margin: 20px auto;
  background-color: rgb(255, 255, 255);
  padding: 30px;
  border-radius: 10px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.users_wrap {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 0;
}
